          <template #title>
            <div class='g_flex'>
              <div>
                <span>{{ item.startTime  | dataFilter('MM/DD') }} - {{ item.endTime  | dataFilter('MM/DD') }}</span>
                <span class="font_style">生活</span>
              </div>
              <!-- @click="goLife(lifeCostBills.length,i,item)" -->
