      class='btn_refund'
      @click='roomRent'
    >确定退租</van-button>
    <van-popup
      v-model="show_bottom"
      position="bottom"
    >
      <div
        class="p12 g_bb"
        @click="editOrder"
      >编辑账单</div>
      <div
        v-if="itemOrderIsignore == false"
        class="p12 g_c_FF5 g_bb"
        @click="ignorec"
      >忽略此账单</div>
      <div
        v-if="itemOrderIsignore == true"
