<script>
import { MessageBox, Toast } from 'mint-ui';
import { dataFilter } from '@/utils/filters';
import { GetBusinessOrderroomLeaveinfo, PostBusinessOrderroomLeave } from '@/api/api_business_hotel_and_scale';
import { GetBusinessCooperOrderroomLeaveInfo, PostBusinessCooperOrderroomLeave } from '@/api/api_business';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            info: null,
            lifeCostBills: [],
            rentalBills: [],
            depositReason: null,
            comeTotal: null,
            currenTime: dayjs().format('YYYY-MM-DD'),
            orderDepositRefund: 0,
            orderRoomRefuse: 0,
            orderRemainElecAmount: 0,
            lifeStatus: [],
            res: null,
            newData: null,
            newOrder: null,
            c_time: null,
            myValue: '550',
            renson: null,
            leaveRuleTitle: '提前退房不退房费不退押金',
            uPrice: null,
            aaa: null,
            show_bottom: false,
            itemOrderIsignore: false
        };
    },
    created() {
        this.$store.commit('setHeader', '退租结算');
        this.c_time = dataFilter(dayjs(), 'YYYY年MM月DD日');
        if (this.$route.query.cooperOrderRoomId) {
            this.getCooperRentCount(this.$route.query.cooperOrderRoomId);
        } else {
            this.getRentCount(this.$route.query.orderRoomId);
        }
    },
    computed: {
        computedOrderDepositRefund: {
            get: function () {
                this.aaa = null;
                let all = Number(this.orderDepositRefund) - Number(this.comeTotal) < 0 ? 0 : Number(this.orderDepositRefund) - Number(this.comeTotal);
                return Number(all).toFixed(2);
            },
            set: function (val) {
                console.log(val);
                this.aaa = Number(val);
            }
        },
        // 应退租金: orderRoomRefuse  扣押金额: aaa   应退电费:orderRemainElecAmount
        bottomTotal() {
            // let _deposit = Number(this.orderDepositRefund) - Number(this.comeTotal) <= 0 ? 0 : Number(this.orderDepositRefund) - Number(this.comeTotal);
            // let _deposit_ = this.aaa || _deposit;
            // let _deposit_ = this.aaa || 0;
            // _deposit_ = _deposit_ <= 0 ? 0 : _deposit_;
            // let all = Number(this.orderRoomRefuse) + Number(_deposit_) + Number(this.orderRemainElecAmount);
            let all = Number(this.orderRoomRefuse) + Number(this.orderRemainElecAmount);
            this.saveBottomTotal = Number(all).toFixed(2);
            return Number(all).toFixed(2);
        }
        // backTotal() {
        //     // let _deposit = Number(this.orderDepositRefund) - Number(this.comeTotal) < 0 ? 0 : Number(this.orderDepositRefund) - Number(this.comeTotal);
        //     // let _deposit_ = this.aaa || _deposit;
        //     let _deposit_ = this.aaa || 0;
        //     // _deposit_ = _deposit_ < 0 ? 0 : _deposit_;
        //     // let all = Number(this.orderRoomRefuse) + Number(_deposit_) + Number(this.orderRemainElecAmount) - Number(this.comeTotal);
        //     let all = Number(this.orderRoomRefuse) + Number(this.orderRemainElecAmount) - Number(this.comeTotal);
        //     return Number(all).toFixed(2);
        // }
    },
    watch: {
        aaa(neww) {
            if (neww > this.newData.deposit) {
                Toast({
                    // message: `不能超过${this.newOrder.depositRefund}元`,
                    message: `不能超过${this.newData.deposit}元`,
                    duration: 900
                });
                this.aaa = 0;
            }
        }
    },
    methods: {
        // 获取退房详细信息
        getRentCount(id) {
            GetBusinessOrderroomLeaveinfo(id).done((res) => {
                this.getDeal(res);
            });
        },
        // 获取单位订单退房详细信息
        getCooperRentCount(id) {
            GetBusinessCooperOrderroomLeaveInfo({ cooperOrderRoomId: id }).done((res) => {
                this.getDeal(res);
            });
        },
        // 处理数据
        getDeal(res) {
            this.newData = res.data;
            this.uPrice = Number(res.data.order.price / (res.data.order.paymentMethod * 30)).toFixed(2);
            if (res.data.order.rule) {
                let orderRule = JSON.parse(res.data.order.rule);
                if (!orderRule.leaveRule) {
                    this.leaveRuleTitle = '提前退房不退房费不退押金';
                } else {
                    if (orderRule.roomPrice == '0' && orderRule.deposit == '0') {
                        this.leaveRuleTitle = '提前退房不退房费不退押金';
                    } else if (orderRule.roomPrice == '0' && orderRule.deposit == '1') {
                        this.leaveRuleTitle = '提前退房不退房费退押金';
                    } else if (orderRule.roomPrice == '1' && orderRule.deposit == '0') {
                        this.leaveRuleTitle = '提前退房退房费不退押金';
                    } else if (orderRule.roomPrice == '1' && orderRule.deposit == '1') {
                        this.leaveRuleTitle = '提前退房退房费退押金';
                    }
                }
            }
            this.newOrder = res.order;
            this.depositReason = res.order.depositCutReason;
            // this.orderRoomRefuse = res.order.roomRefuse;
            this.orderRoomRefuse = Number(res.order.roomRefuse).toFixed(2);
            // this.orderDepositRefund = res.order.depositRefund;
            this.orderDepositRefund = res.data.deposit;
            this.orderRemainElecAmount = Number(res.order.remainElecAmount).toFixed(2);
            this.lifeCostBills = res.order.cooperLifeCostBills || res.order.lifeCostBills;
            this.lifeCount = 0;
            // 未抄表
            for (var i = 0; i < this.lifeCostBills.length; i++) {
                this.lifeCostBills[i].isIgnore = false;
                this.lifeCount += this.lifeCostBills[i].totalPrice;
                this.lifeCostBills[i].set_round = i;
                this.lifeCostBills[i].set_length = this.lifeCostBills.length;
            }
            this.rentalBills = res.order.cooperRentalBills || res.order.rentalBills;
            this.rentCount = 0;
            for (let i = 0; i < this.rentalBills.length; i++) {
                this.rentalBills[i].isIgnore = false;
                if (this.rentalBills[i].payStatus == 0) {
                    this.rentCount += this.rentalBills[i].roomPrice;
                }
            }
            this.comeTotal = Number(this.lifeCount + this.rentCount).toFixed(2);
        },
        goNext(type, length, i, item) {
            this.itemOrder = item;
            this.itemOrderIsignore = this.itemOrder.isIgnore;
            this.length = length;
            this.index = i;
            this.type = type;
            this.show_bottom = true;
        },
        editOrder() {
            if (this.type == 'life') {
                // 清空未超表生活账单信息
                localStorage.removeItem('life_fee');
                this.$router.push({
                    path: '/lifeFeetrans',
                    query: {
                        lifeCostBillId: this.itemOrder.id,
                        set_length: this.length,
                        set_round: this.index
                    }
                });
            } else {
                if (this.$route.query.cooperOrderRoomId) {
                    this.$router.push({
                        path: '/roomFeeD',
                        query: {
                            cooperRentalBillId: this.itemOrder.id
                        }
                    });
                } else {
                    this.$router.push({
                        path: '/roomFeeD',
                        query: {
                            rentalBillId: this.itemOrder.id
                        }
                    });
                }
            }
            this.show_bottom = false;
        },
        ignorec() {
            if (this.type == 'life') {
                this.lifeCostBills.find((item) => item.id == item.id).isIgnore = true;
                this.comeTotal -= this.itemOrder.totalPrice;
            } else {
                this.rentalBills.find((item) => item.id == item.id).isIgnore = true;
                this.comeTotal -= this.itemOrder.roomPrice;
            }
            this.show_bottom = false;
        },
        recoveryOrder() {
            if (this.type == 'life') {
                this.lifeCostBills.find((item) => item.id == item.id).isIgnore = false;
                this.comeTotal += this.itemOrder.totalPrice;
            } else {
                this.rentalBills.find((item) => item.id == item.id).isIgnore = false;
                this.comeTotal += this.itemOrder.roomPrice;
            }
            this.show_bottom = false;
        },
        // 单位订单房间退房/退租
        roomRent() {
            MessageBox.confirm('', {
                message: '退租后剩余租房押金将退回给租客，请谨慎操作！',
                title: '确定退租？',
                cancelButtonText: '取消',
                confirmButtonText: '确定'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        // let _deposit = Number(this.orderDepositRefund) - Number(this.comeTotal) < 0 ? 0 : Number(this.orderDepositRefund) - Number(this.comeTotal);
                        // let _deposit_ = this.aaa || _deposit;
                        // _deposit_ = _deposit_ < 0 ? 0 : _deposit_;
                        let data = {
                            orderRoomId: this.newData.id,
                            orderId: this.newData.orderId,
                            depositCut: this.aaa ? this.aaa : 0,
                            depositCutReason: this.depositReason
                        };
                        let _api = this.$route.query.cooperOrderRoomId ? PostBusinessCooperOrderroomLeave : PostBusinessOrderroomLeave;
                        _api(data).done((res) => {
                            Toast({
                                message: res.msg,
                                duration: 900
                            });
                            setTimeout('window.history.go(-1)', 900);
                        });
                    }
                })
                .catch((e) => {
                    console.log(e);
                });
        }
    }
};
</script>
<style scoped>
#rentCount_box {
    font-size: 14px;
    overflow: auto;
    height: 84%;
}
::v-deep .van-cell-group {
    background: whitesmoke;
}
::v-deep .van-cell__title {
    text-align: left;
    color: #000;
}
::v-deep .van-cell__value {
    color: #000;
}
.need_refund {
    text-align: right;
    padding: 16px;
    font-weight: bold;
}
.btn_refund {
    background: #ff7d41;
    width: 100%;
    position: fixed;
    bottom: 0;
    left: 0;
    color: #fff;
}
.font_style {
    border: 1px solid #ff7d41;
    color: #ff7d41;
    font-size: 12px;
    padding: 1px 2px;
    border-radius: 2px;
    margin-left: 5px;
}
.c_f4 {
    color: #888;
}
.f_bold {
    font-weight: bold;
}
.p12 {
    padding: 12px;
}
.kongbai {
    background: #f4f4f4;
    height: 10px;
}
</style>



